import React from 'react';
import logo from '../../assets/images/logostajnia.png';
import './MainPage.css';

const MainPage = (props) => {
    return(
        <div className="MainPage">
            <img src={logo} alt="logo"/>
            <h1>Witaj na stronie stajni Kasztanka!</h1>
            <p>
                Poznaj nasz szerok ofert - w zakadkach na grze strony moesz zobaczy list mieszkajcych u nas koni oraz harmonogram odbywajcych si jazd. 
            </p>
            <p>
                Jeli jeste wacicielem rezydujcego u nas konia - zaloguj si, by zyska dostp do dodatkowych opcji! Bdziesz mg zakwaterowa kolejnego konia, zapisa si na zajcia a take zgosi kontuzj swojego zwierzcia, chocia mamy nadziej e to nie bdzie konieczne! Ze swojej strony dooymy wszelkich stara, aby twj czterokopytny przyjaciel czu si u nas jak najlepiej.
            </p>
        </div> 
    );
}

export default MainPage;


