import React, {Component} from 'react';
import InputFrame from '../../../components/Forms/InputFrame/InputFrame';
import {connect} from 'react-redux';
import axios from '../../../axiosInstance';


class ChangeWorker extends Component {

    state = {
        newPass: '',
        newPassRep: '',
        current: ''
    }

    onChangeCurrent = (event) => {
        this.setState({
            current: event.target.value});
    }

    onChangePass = (event) => {
        this.setState({
            newPass: event.target.value});
    }

    onChangeRep = (event) => {
        this.setState({
            newPassRep: event.target.value});
    }

    onSubmitHandler = (event) => {
        event.preventDefault();
        if(this.state.current !== this.props.password){
            alert("Niestety, bdnie wpisae obecne haso - sprbuj ponownie!")
        }else if(this.state.newPass === this.props.current){
            alert("Nowe haso nie moe by takie samo jak obecne - prosz, wymyl inne!")
        }else if(this.state.newPass !== this.state.newPassRep){
            alert("Pola z nowym hasem nie zawieraj takiej samej wartoci - sprawd czy si nie pomylie")
        }else{
            console.log(this.state);
            axios.put('/haslo/'+this.props.idUser, this.state)
            .then(response => {
                console.log(response)
                if(response.data.results){
                    alert("Twoje haso zostao zmienione!!");
                }else{
                    alert("Niestety, co poszo nie tak!")
                }
            })
            .catch(error => {
                alert("Niestety, nie udao si zmieni hasa! Sprawd poprawno danych lub sprbuj ponownie pniej")
                console.log(error);
            })
            }
        
    } 

    render(){
        return(
            <InputFrame>
                <form onSubmit={this.onSubmitHandler}>
                    <h3>Zmie haso do swojego konta:</h3>
                    <input type="text" name="curpass" onChange={this.onChangeCurrent} required/><br/>Obecne haso<br/><br/>
                    <input type="text" name="pass1" onChange={this.onChangePass} required/><br/>Nowe haso<br/><br/>
                    <input type="text" name="pass2" onChange={this.onChangeRep} required/><br/>Powtrz nowe haso<br/><br/>
                    <input type="submit" value="Zatwierd zmian"/>
                </form>
            </InputFrame>
        );
    }
}

const mapStateToProps = state => {
    return {
        login: state.login,
        idUser: state.idUser,
        password: state.password,
        isAuth: state.isAuth
    }
}

export default connect(mapStateToProps)(ChangeWorker);


